@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.js-scroll__inner-layout {
  width: 12.5rem;
  background: #333;
  transition: 1s all linear;
}

.js-scroll__inner-layout:hover {
  background: #fff;
}

.js-scroll__inner-layout::before {
  content: "";
  display: block;
  padding-top: 100%;
}

.js-scroll-top__arrow {
  display: inline-block;
  position: relative;
}

.js-scroll-top__arrow::before {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background: #f00;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-84%, -50%) rotate(-45deg);
}

.js-scroll-top__arrow::after {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background: #f00;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-16%, -50%) rotate(45deg);
}

.js-scroll-top__arrow:hover:before,
.js-scroll-top__arrow:hover:after {
  background: #333;
  transition: 1s all linear;
}

.js-scroll-top__layout {
  margin-bottom: 3.125rem;
  overflow: hidden;
}
.scroll-top__arrow {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #333;
  transition: 1s all linear;
}

.scroll-top__arrow:hover {
  background: #fff;
}

.scroll-top__arrow:hover:before,
.scroll-top__arrow:hover:after {
  background: #333;
}

.scroll-top__arrow::before {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background: #fff;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-15%, -50%) rotate(45deg);
  transition: 1s all linear;
}

.scroll-top__arrow::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background: #fff;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-85%, -150%) rotate(-45deg);
  transition: 1s all linear;
}

.scroll-top__height {
  position: relative;
}

.scroll-top__height::before {
  content: "";
  display: block;
  padding-top: 100%;
}

.scroll-top__inner-layout {
  width: 12.5rem;
}
.scroll-top__layout {
  margin-bottom: 3.125rem;
}
.scroll-top {
  display: inline-block;
}
